.caravan-section-exploration {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: space-around;

    /* outline: 1px solid white; */
}

.caravan-section-exploration .resource-picker-parent{
    width: 40%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.caravan-section-exploration .combat-prep-parent{
    width: 40%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.caravan-section-exploration .venture-out-button{
    width: 100%;
    height: 7%;
    margin-left: 5%;

    font-size: var(--nav-button-font-size);
    background: none;
    border: none;
    color: var(--outline-color);
}
.caravan-section-exploration .venture-out-button:hover{
    text-decoration: underline 2px solid var(--outline-color);
}

.venture-button-spacing-matcher{
    width: 100%;
    height: 7%;
    margin-left: 5%;

    font-size: var(--nav-button-font-size);
    background: none;
    border: none;
    color: var(--outline-color);
}